@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
header{
    text-align: center;
}
.header-logo{
    float: left;
    font-size: 25px;
    margin: 0;
}
.navbar{
    background-color: #dc7f9b;
    text-align: end;
    padding: 15px;
    height: 60px;
}
.navbar a{
    text-decoration: none;
    text-shadow: 2px 2px #4e0f4e ;
    color: white;
    padding: 5px;
    line-height: 30px;
}
.navbar a:hover{
    text-decoration: none;
    color: #f5b9c7;
}
nav{
    display: inline-block
}
.logo{
    text-shadow: 2px 2px #8f7875 ;
    color: #f9f1f1;
}
.user{
    text-decoration: none;
    text-shadow: 2px 2px #8f7875 ;
    color: #f9f1f1;
    padding: 5px;
    line-height: 30px;
    border-right: 2px solid  #f9f1f1;
}
.Input{
    border-radius: 5px;
    padding: 2px 2px 2px 2px;
}
button{
    font-weight: bolder;
    border: none;
    border-radius: 15px;
    color: #6f1f37;
    width: 100px;
    height: 35px;
    margin: 5px;
    font-size: 15px;
    background-color: #f6dfe6;
    display: inherit;
    font-family: 'Fredoka One';
    padding: 4px 8px;
    margin: 0 auto;
    margin-top: 5px;
}
.box{
    text-align: center;
    width: 300px;
    border-radius: 7px;
    padding: 25px;
    margin: 20px;
    margin-left: auto;
    margin-right: auto;
    background-color: #dc7f9b;
    text-shadow: 2px 2px #391441 ;
    color: white;
    font-weight: bold;
}

.link{
    text-decoration: none;
    text-shadow: 2px 2px #4f1d6b ;
    color: #fff1f1;
    padding: 10px;
    line-height: 30px;
}

.link:hover{
    color: #f5b9c7;
}
