* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	font-family: 'Fredoka One', cursive;
	-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f6dfe6;
  color: #6f1f37;

}

:root {
	--font-size: calc(10px + 1vmin);
}

